/*
  API helper for dashboard
*/
import type { Product } from "sam-shared";

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE ?? "http://localhost:3000";

/**
 * Fetch product list.
 * If the backend supports pagination & query params you can supply them via the `params` object,
 * otherwise the helper will just perform a simple GET.
 */
export async function listProducts(params: Record<string, string | number> = {}) {
  const url = new URL("/products", API_BASE);
  Object.entries(params).forEach(([key, value]) => {
    if (value !== undefined && value !== null && value !== "") {
      url.searchParams.set(key, String(value));
    }
  });

  const res = await fetch(url.toString(), {
    cache: "no-store",
  });

  if (!res.ok) {
    throw new Error(`Failed to fetch products: ${res.status} ${res.statusText}`);
  }

  return (await res.json()) as {
    success?: boolean;
    data: Product[];
    total?: number;
  };
}

/**
 * Parse HAR file text to product array on the backend.
 */
export async function parseHarFile(harText: string): Promise<Product[]> {
  const res = await fetch(`${API_BASE}/products/parse`, {
    method: "POST",
    headers: {
      "Content-Type": "text/plain",
    },
    body: harText,
  });

  if (!res.ok) {
    const text = await res.text();
    throw new Error(`Failed to parse HAR: ${text}`);
  }

  return (await res.json()) as Product[];
}

/**
 * Upsert products to backend.
 */
export async function upsertProducts(products: Product[]): Promise<void> {
  const res = await fetch(`${API_BASE}/products/upsert`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(products),
  });

  if (!res.ok) {
    const text = await res.text();
    throw new Error(`Failed to upsert products: ${text}`);
  }
} 