"use client";

import { useEffect, useMemo, useState } from "react";
import { listProducts } from "@/lib/api";
import type { Product } from "sam-shared";
import {
  Table,
  TableHeader,
  TableBody,
  TableHead,
  TableRow,
  TableCell,
} from "@/components/ui/table";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";

const PAGE_SIZE = 20;

export default function ProductListPage() {
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(false);
  const [search, setSearch] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [error, setError] = useState<string | null>(null);

  const fetchData = async () => {
    try {
      setLoading(true);
      const resp = await listProducts();
      setProducts(resp.data ?? []);
    } catch (err) {
      console.error(err);
      setError((err as Error).message);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  const filteredProducts = useMemo(() => {
    if (!search.trim()) return products;
    return products.filter((p) =>
      (p.title ?? "").toLowerCase().includes(search.toLowerCase()),
    );
  }, [products, search]);

  const paginatedProducts = useMemo(() => {
    const start = (currentPage - 1) * PAGE_SIZE;
    return filteredProducts.slice(start, start + PAGE_SIZE);
  }, [filteredProducts, currentPage]);

  const totalPages = Math.max(1, Math.ceil(filteredProducts.length / PAGE_SIZE));

  return (
    <div className="p-6 space-y-6">
      <h1 className="text-2xl font-semibold">商品列表</h1>

      <div className="flex gap-4 items-center">
        <Input
          placeholder="搜索商品标题"
          value={search}
          onChange={(e) => {
            setSearch(e.target.value);
            setCurrentPage(1);
          }}
          className="max-w-xs"
        />
        <Button onClick={fetchData} disabled={loading} size="sm" variant="secondary">
          刷新
        </Button>
      </div>

      {error && (
        <p className="text-destructive text-sm">{error}</p>
      )}

      <div className="border rounded-md">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>图片</TableHead>
              <TableHead>SPU</TableHead>
              <TableHead>标题</TableHead>
              <TableHead>价格</TableHead>
              <TableHead>库存</TableHead>
              <TableHead>是否可售</TableHead>
              <TableHead>是否进口</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {paginatedProducts.map((p) => (
              <TableRow key={`${p.spuId}-${p.storeId}`}>
                <TableCell>
                  {p.image ? (
                    <img
                      src={p.image}
                      alt={p.title ?? ""}
                      className="w-16 h-16 object-contain rounded"
                      width={60}
                      height={60}
                    />
                  ) : (
                    "-"
                  )}
                </TableCell>
                <TableCell>{p.spuId}</TableCell>
                <TableCell>{p.title}</TableCell>
                <TableCell>{p.priceInfo?.[0]?.price ?? "-"}</TableCell>
                <TableCell>{p.stockInfo?.stockQuantity ?? "-"}</TableCell>
                <TableCell>{p.isAvailable ? "是" : "否"}</TableCell>
                <TableCell>{p.isImport ? "是" : "否"}</TableCell>
              </TableRow>
            ))}
            {paginatedProducts.length === 0 && !loading && (
              <TableRow>
                <TableCell colSpan={7} className="text-center py-4">
                  无数据
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </div>

      {/* Pagination controls */}
      <div className="flex justify-between items-center">
        <span>
          第 {currentPage} / {totalPages} 页
        </span>
        <div className="flex gap-2">
          <Button
            size="sm"
            variant="outline"
            onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
            disabled={currentPage === 1}
          >
            上一页
          </Button>
          <Button
            size="sm"
            variant="outline"
            onClick={() => setCurrentPage((p) => Math.min(totalPages, p + 1))}
            disabled={currentPage === totalPages}
          >
            下一页
          </Button>
        </div>
      </div>
    </div>
  );
} 